:root {
    --primary-color: #e0c521;
  }
  
  .btn, btn.new {
    background-color: #f5bd07;
    padding: 10px 0;
    font-size: 0.84rem;
    text-align: center;
    border: 0;
    outline: none;
    border-radius: 10px;
    width: 120px;
    margin-left: 5px;
    cursor: pointer;
    color:#fff;
    font-weight:600;
  }
  
  .btn.add {
    background-color: #f5bd07;
    font-size:1.5rem;
    line-height:0.66em;
  }
  
  .btn.remove {
    font-size:1.5rem;
    background-color: #4769ff;
    line-height:0.66em;
  }
  
  .btn:active {
    transform: scale(0.98);
  }
