:root {
  --primary-color: #f5bd07;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0.5rem 0.3rem;
  margin: 10px 0;
  position: relative;
  max-width:160px;
}

.card__title {
  font-weight: 400;
  text-align: center;
}

.card__title .card__price {
  font-weight: bold;
}

.image__container {
  width: 160px;
  height: 160px;
  margin: 0 auto;
}

.image__container img {
  width: 100%;
  object-fit: cover;
}

.btn-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.card__price {
  text-align: center;
  display: inline-block;
}

.card__badge {
  position: absolute;
  top: 0;
  right: 0;
  width: 30px;
  height: 30px;
  background-color: rgb(71, 105, 255);
  border-radius: 50%;
  transform: translate(10px, -15px) scale(1.05);
  color: #fff;
  font-weight: bold;
  text-align: center;
  border: 2px solid rgb(71, 105, 255);
  font-size: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  pointer-events:all;
  opacity:1;
  transition:opacity 0.3 ease;
}

.card__badge--hidden {
  display:none;
  opacity:0;
  pointer-events:none;
}
